<script>
import { onMount } from 'svelte';
//import 'crypto'
//import { subtle, getRandomValues } from 'crypto'.webcrypto;
//require('crypto').webcrypto
//import { webcrypto } from 'crypto'
import {getNmcliGeneral,getIpAvahi
  //,postPublicKey
    ,setApiConfigUrlAvahi,setApiConfigUrl,getApiConfigUrl,getApiConfigPort
    ,postReboot,postShutDown, postUpGrade} from './api/nmcli.js'
import { Svelma,Button,Dialog } from 'svelma'
import {unitCrypto,LoginStore,unitStore} from './stores.js'

import { emailValidator, requiredValidator, sensorIdValidator, ipAddressNumValidator } from './validators.js'
import { createFieldValidator } from './validation.js'
//const [ validity, validate ] = createFieldValidator(requiredValidator(), emailValidator(), sensorIdValidator())
const [ sensorIdValidity, sensorIdValidate ] = createFieldValidator(sensorIdValidator())
const [ ipAddressNumValidity, ipAddressNumValidate ] = createFieldValidator(ipAddressNumValidator())

const updateCrypto = function(iv,ivDate){
  if (unitCrypto.sensorId!=sensorId || iv!=unitCrypto.iv){
    //console.log('set crypto iv')
    let t=sensorId+'ScapelerApriSensor'+"                                "
    unitCrypto.key=t.substr(0,32)
//    unitCrypto.simpleCrypto = new SimpleCrypto(unitCrypto.key)

  	unitCrypto.iv = iv //crypto.randomBytes(16);
  	unitCrypto.ivDate= ivDate //new Date()
    //console.log(unitCrypto)
    //console.dir(crypto)
//  	unitCrypto.cipher = crypto.createCipheriv(unitCrypto.algoritm, unitCrypto.key, unitCrypto.iv)
//  	unitCrypto.decipher = crypto.createDecipheriv(unitCrypto.algoritm, unitCrypto.key, unitCrypto.iv)
//    unitCrypto.cipher = crypto.createCipheriv(unitCrypto.algoritm, Buffer.from(unitCrypto.key), unitCrypto.iv)
//  	unitCrypto.decipher = crypto.createDecipheriv(unitCrypto.algoritm, Buffer.from(unitCrypto.key), unitCrypto.iv)
  }
}

//import { Button } from 'svelma'

const protocol='http://'

const nmcliGeneralInit={
  "CONNECTIVITY":'?'
  ,"STATE":'Not connected'
  ,"WIFI":'?'
  ,"WIFI-HW":'?'
  ,"WWAN":'?'
  ,"WWAN-HW":'?'
}
let nmcliGeneralState=''
let connectionStatus='is-info'
const unit = {
  nmcliGeneral: nmcliGeneralInit
}

//let selectedUrlAvahi=''
//let ipAddressAvahi='10.42.0.1'
//setApiConfigUrl(protocol+ipAddressAvahi)
let sensorId=''
let ipAddress=''
let serviceUrl=''

let isGettingAvahi=''
let getAvahiLatest=new Date()
let getIpAddressLatest= new Date()
let inputRefSensorId
let inputRefIpAddress

const timerGetNmcliGeneral = function(){
  serviceUrl=getApiConfigUrl()
/*
  if (sensorId=='' && unitStore.unitId!='') {
    sensorId=unitStore.unitId
    console.log('x'+sensorId+'x')
    setApiConfigUrlAvahi(protocol+sensorId+'.local')
    //confirm({action:'ipAddressAvahi'})
  }
  console.log()
*/

  getNmcliGeneral()
  .then((result)=>{
    var props
    unit.nmcliGeneral=result.data
    updateCrypto(unit.nmcliGeneral.iv,unit.nmcliGeneral.ivDate)
    if (nmcliGeneralState!==unit.nmcliGeneral.STATE) {
      nmcliGeneralState=unit.nmcliGeneral.STATE
      if (nmcliGeneralState.substr(0,9)=='connected') {
        connectionStatus='is-success'
        props={ type: 'is-success', position: 'is-bottom-right', icon: true, duration:3000 }
        Svelma.Notification.create({
          message: 'Er is (weer) verbinding met de sensorkit! ',
          ...props
        })
      } else {
        connectionStatus='is-danger'
        props={ type: 'is-warning', position: 'is-bottom-right', icon: true, duration:3000 }
        Svelma.Notification.create({
          message: 'Er is geen verbinding met de sensorkit.',
          ...props
        })
      }
    }
  })
  .catch((error)=> {
    var props
    console.log('getNmcliGeneral catch')
    console.log(error)
    connectionStatus='is-danger'
    if (nmcliGeneralState!=='timeout') {
      nmcliGeneralState='timeout'
      props={ type: 'is-danger', position: 'is-bottom-right', icon: true, duration:3000 }
      Svelma.Notification.create({
        message: 'Er is geen verbinding met de sensorkit! ',
        ...props
      })
    }
    unit.nmcliGeneral=nmcliGeneralInit
  })
}

function confirm(param) {
   switch(param.action) {
     case 'ipAddressAvahi':
        isGettingAvahi='is-loading'
        //postPublicKey()

        ipAddress=''
        setApiConfigUrlAvahi(protocol+sensorId+'.local')
        getIpAvahi()
        .then((result)=>{
//            console.dir(JSON.parse(result))
//            console.dir(result.data)
            ipAddress=result.data.ipAvahi
            setApiConfigUrl(protocol+sensorId+'.local')
            isGettingAvahi=''
            var props={ type: 'is-success', position: 'is-bottom-right', icon: true, duration:4000 }
            Svelma.Notification.create({
              message: 'Met ID ' + sensorId + ' is IP-adres '+ipAddress+' gevonden.',
              ...props
            })
        })
        .catch((error)=>{
//          console.log('getIpAvahi catch')
//          console.log(error)
          isGettingAvahi=''
          var props={ type: 'is-danger', position: 'is-bottom-right', icon: true, duration:4000 }
          Svelma.Notification.create({
            message: 'Opvragen IP-adres sensorId ' + sensorId + ' is niet gelukt.',
            ...props
          })
        })
        return
    case 'useIpAddress':
      setApiConfigUrl(protocol+ipAddress)
      return
    case 'reboot':
      return Dialog.confirm({
        message: 'Wilt u de sensorkit herstarten?',
        title: "Herstart sensorkit (reboot)",
        type: 'is-info',
        icon: 'times-circle'
      })
      .then(result=>{
        console.log('then')
        if (result) {
          postReboot()
        }
      })
      .catch(error=>{
        console.log('catch')
      })
      return
    case 'shutdown':
      return Dialog.confirm({
        message: 'Wilt u de sensorkit stoppen?',
        title: "Stop sensorkit (shutdown)",
        type: 'is-info',
        icon: 'times-circle'
      })
      .then(result=>{
        console.log('then')
        if (result) {
          postShutDown()
        }
      })
      .catch(error=>{
        console.log('catch')
      })
      return
    case 'upgrade':
      return Dialog.confirm({
        message: 'Wilt u de sensorkit upgraden?',
        title: "Nieuwe versie software installeren (upgrade)",
        type: 'is-info',
        icon: 'times-circle'
      })
      .then(result=>{
        console.log('then')
        if (result) {
          postUpGrade()
        }
      })
      .catch(error=>{
        console.log('catch')
      })
      return
    default:
      Dialog.confirm('Default action:none')
      .then(thenHandler)
   }
 }

onMount(async () => {
  // get nmcli general info when module is activated like status of network
  console.log('onmount van nmcli-general start')
  var currentUrl = window.location;

  unitStore.unitId=''
  unitStore.unitUrl='10.42.0.1'

  if (currentUrl.hostname.substr(4,6)=='.local') {
    unitStore.unitId=currentUrl.hostname.substr(0,4)
    sensorId = unitStore.unitId
    setApiConfigUrlAvahi(protocol+sensorId+'.local')
    unitStore.unitUrl=currentUrl.hostname
    serviceUrl = currentUrl.hostname
    confirm({action:'ipAddressAvahi'})
  } else {
    if (currentUrl.hostname=='localhost' || currentUrl.hostname=='nm.aprisensor.nl' ) {
      ipAddress = unitStore.unitUrl
      serviceUrl = unitStore.unitUrl
      confirm({action:'useIpAddress'})
    } else {
      // currentUrl.hostname!='10.42.0.1' or lokal ip-address
      unitStore.unitUrl=currentUrl.hostname
      ipAddress = currentUrl.hostname
      serviceUrl = currentUrl.hostname
      confirm({action:'useIpAddress'})
    }
  }

  console.log('onmount van nmcli-general before timerGetNmcliGeneral')
  timerGetNmcliGeneral()
  setInterval(timerGetNmcliGeneral, 3000);
  console.log('onmount van nmcli-general end')
});

</script>

<!--section class="section" transition:slide-->
