    <h1>3. Formulier koppelen aan sensorkit service</h1>
      <div class="info-blok">
        <span>Dit configuratieformulier maakt gebruik van een service welke door
          de sensorkit wordt aangeboden. Daarvoor moet nog wel aangegeven
          worden waar deze service te vinden is en dat gebeurt met het ID of met
          een IP-adres zoals al eerder aangegeven bij punt 2 (manier A of B).<br/>
          In geval manier A is toegepast (met ID) dan zal onderstaande in de
          meeste gevallen al automatisch gevuld zijn. Is dat niet het geval,
          vul dan het ID in (manier A) of het IP-adres (manier B).</span>
        <div class="columns is-mobile is-centered">
          <div class="column is-narrow">
            <table class="table is-striped table is-narrow connectivity">
              <thead>
                <tr>
                  <th>ID:</th>
                  <th></th>
                  <th>IP-adres:</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <div class=''>
                      <input placeholder="ID?"
                        size="4"
                        maxlength="4"
                        title="ID van sensorkit: als combinatie van 4 cijfers of letters"
                        bind:this={inputRefSensorId}
                        bind:value={sensorId}
                        use:sensorIdValidate={sensorId}
                        class:field-danger={!$sensorIdValidity.valid}
                        class:field-success={$sensorIdValidity.valid}
                        on:keypress={(e) =>
                          {
                            var props={}
                            // no double call
                            if (new Date().getTime()-getAvahiLatest.getTime() < 1000) {
                              return
                            }
                            getAvahiLatest=new Date()

                            if (e.charCode == 13){
                              if (sensorId && $sensorIdValidity.valid==true) {
                                props={ type: 'is-info', position: 'is-bottom-right', icon: true, duration:4000 }
                                Svelma.Notification.create({
                                  message: 'Met ID ' + sensorId + ' wordt geprobeerd om een IP-adres op te vragen.',
                                  ...props
                                })
                                confirm({action:'ipAddressAvahi'})
                              } else {
                                props={ type: 'is-warning', position: 'is-bottom-right', icon: true, duration:4000 }
                                Svelma.Notification.create({
                                  message: 'Het opgegeven ID is niet juist (combinatie vier letter of cijfers)',
                                  ...props
                                })
                              }
                            }
                          }
                        }
                        on:change={(e) =>
                        {
                          var props={}
                          // no double call
                          if (new Date().getTime()-getAvahiLatest.getTime() < 1000) {
                            return
                          }
                          getAvahiLatest=new Date()
                          if (sensorId && $sensorIdValidity.valid==true) {
                            props={ type: 'is-info', position: 'is-bottom-right', icon: true, duration:4000 }
                            Svelma.Notification.create({
                              message: 'Met ID ' + sensorId + ' wordt geprobeerd om een IP-adres op te vragen.',
                              ...props
                            })
                            confirm({action:'ipAddressAvahi'})
                          }
                        }}
                        on:blur={(e) => {
                          if (sensorId==undefined || sensorId==null || sensorId=='') return
                          if (sensorId && $sensorIdValidity.valid==false) {
                            inputRefSensorId.focus()
                          }
                        }}
                      />
                    </div>
                  </td>
                  <td><div class=''>of</div></td>
                  <td>
                    <div class=''>
                      <input placeholder="10.42.0.1"
                        maxlength="15" size="15"
                        title="IP-adres: vier cijfers gescheiden door een punt"
                        bind:this={inputRefIpAddress}
                        bind:value={ipAddress}
                        use:ipAddressNumValidate={ipAddress}
                        class:field-danger={!$ipAddressNumValidity.valid}
                        class:field-success={$ipAddressNumValidity.valid}
                        on:keypress={(e) =>
                          {
                            var props={}
                            // no double call
                            if (new Date().getTime()-getIpAddressLatest.getTime() < 1000) {
                              return
                            }
                            getIpAddressLatest=new Date()

                            if (e.charCode == 13){
                              if (ipAddress && $ipAddressNumValidity.valid==true) {
                                props={ type: 'is-info', position: 'is-bottom-right', icon: true, duration:4000 }
                                Svelma.Notification.create({
                                  message: 'Met IP-adress ' + ipAddress + ' wordt geprobeerd contact te maken met de sensorkit.',
                                  ...props
                                })
                                confirm({action:'useIpAddress'})
                              } else {
                                props={ type: 'is-warning', position: 'is-bottom-right', icon: true, duration:4000 }
                                Svelma.Notification.create({
                                  message: 'Het opgegeven IP-adres is niet correct (voorbeeld: 10.42.0.1 of 192.168.0.100)',
                                  ...props
                                })
                              }
                            }
                          }
                        }
                        on:change={(e) => {
                          var props={}
                          // no double call
                          if (new Date().getTime()-getIpAddressLatest.getTime() < 1000) {
                            return
                          }
                          getIpAddressLatest=new Date()

                          if (ipAddress && $ipAddressNumValidity.valid==true) {
                            props={ type: 'is-info', position: 'is-bottom-right', icon: true, duration:4000 }
                            Svelma.Notification.create({
                              message: 'Met IP-adress ' + ipAddress + ' wordt geprobeerd contact te maken met de sensorkit.',
                              ...props
                            })
                            confirm({action:'useIpAddress'})
                          } else {
                            props={ type: 'is-warning', position: 'is-bottom-right', icon: true, duration:4000 }
                            Svelma.Notification.create({
                              message: 'Het opgegeven IP-adres is niet correct (voorbeeld: 10.42.0.1 of 192.168.0.100)',
                              ...props
                            })
                          }
                        }}
                        on:blur={(e) => {
                          if (ipAddress==undefined || ipAddress==null || ipAddress=='') return
                          if (ipAddress && $ipAddressNumValidity.valid==false) {
                            inputRefIpAddress.focus()
                          }
                        }}
                      />
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
            {#if $sensorIdValidity.dirty && !$sensorIdValidity.valid}
              <span class="validation-hint">
                INVALID: {$sensorIdValidity.message}. Voorbeeld: A1B2
              </span>
            {/if}
            {#if $ipAddressNumValidity.dirty && !$ipAddressNumValidity.valid}
              <span class="validation-hint">
                INVALID: {$ipAddressNumValidity.message}. Voorbeeld: 10.42.0.1
              </span>
            {/if}
          </div>
        </div>
      </div>
      <div class="info-blok">
        <span>Indien de koppeling gelukt is wordt dat hieronder aangegeven met
