          of IP-adres juist zijn.</span>
      </div>

    <div class="columns is-mobile is-centered">
      <div class="column is-narrow">
    <table class="table is-striped table is-narrow connectivity">
      <thead>
        <tr>
          <th>Connectivity</th>
          <th>Status</th>
          <th>Wifi</th>
    <!--      <th>Wifi-HW</th>
          <th>WWAN</th>
          <th>WWAN-HW</th> -->
        </tr>
      </thead>
      <tbody>
        <tr>
          <td><div class='general connectivity_{unit.nmcliGeneral.CONNECTIVITY}'>{unit.nmcliGeneral.CONNECTIVITY}</div></td>
          <td><div class='general state_{unit.nmcliGeneral.STATE}'>{unit.nmcliGeneral.STATE}</div></td>
          <td><div class='general wifi_{unit.nmcliGeneral.WIFI}'>{unit.nmcliGeneral.WIFI}</div></td>
    <!--      <td><div class='general wifihw_{unit.nmcliGeneral['WIFI-HW']}'>{unit.nmcliGeneral['WIFI-HW']}</div></td>
          <td><div class='general wwan_{unit.nmcliGeneral.WWAN}'>{unit.nmcliGeneral.WWAN}</div></td>
          <td><div class='general wwanhw_{unit.nmcliGeneral['WWAN-HW']}'>{unit.nmcliGeneral['WWAN-HW']}</div></td>
-->        </tr>
      </tbody>
    </table>
    <span >
      Service url: {serviceUrl}
    </span>
  </div>
</div>
