<div class="container">
  <h1>4. Configureren wifi-netwerk</h1>
  <!--div class="columns is-centered"-->
  <div class="info-blok">
  <span>Hier configureert en activeert u de wifi-verbinding op de sensorkit. De
    knop 'Ververs' laat de geconfigureerde netwerken zien.
    Met de knop 'AccessPoint' zal de sensorkit naar accespoint omschakelen,
    een eigen wifi-netwerk starten.<br>
    De knoppen bij de netwerkverbindingen zijn voor het activeren of verwijderen.<br>
    Bij 5. kunt u een nieuw wifi-netwerk toevoegen.</span>
    <br/><br/>
    <i class="fas fa-info"></i><b>Uw wifi-netwerk gewijzigd?:</b>
    <i>Als uw wifi-netwerk is gewijzigd (bv. andere router of nieuw wachtwoord)
      zal de sensorkit geen verbinding meer kunnen maken en omschakelen naar accesspoint.
      Dan opnieuw configureren door eerst het gewijzigde wifi-netwerk te verwijderen
      en het nieuwe / aangepaste netwerk toevoegen.</i>
  </div>
<!--/div-->
  <div class="container">
    <button class="button is-info {isRefreshingConnections}" on:click={nmcliConnectionShow}>Ververs</button>
    <button class="button is-info" on:click={()=> {
      confirm({action:'nmcliDeviceHotspot'})
      }}>AccessPoint</button>
  </div>
  {#if unit.nmcliConnections !=undefined }
    {#if unit.nmcliConnections.error !=undefined }
      <div class='general error'> ERROR: {unit.nmcliConnections.error.message}</div>
    {:else}
    <div class="columns is-mobile is-centered">
      <div class="column is-narrow">
      <table class="table is-striped is-narrow">
        <thead>
          <tr>
            <th>Name</th>
            <th>Device</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {#each unit.nmcliConnections as connection , i}
          {#if connection.TYPE =='wifi' }
            <tr>
              <td>{connection.NAME}</td>
              <td>{connection.DEVICE}</td>
              <td>
                 <span style="font-size: 2em; color: #3298dc;"
                  todo_use:tooltip={{content: Tooltip, text: 'Connect'}}
                  on:click={()=>
                   {confirm({action:'deviceConnect',key:'uuid',connection:connection})}}
                    aria-label="Open">
                    {#if connection.DEVICE=='--'}
                    <Icon icon={iconFaWifi}></Icon>
                     {:else}
                     <Icon icon={iconFaCheck}></Icon>
                    {/if}
                 </span>
                 <span style="font-size: 2em; color: red;"
                 tooltip="Delete"
                 on:click={()=>
                  confirm({action:'deleteConnection',key:'uuid',connection:connection})}
                   aria-label="Delete">
                 <Icon icon={iconFaWindowClose}></Icon>
                 </span>
                 <!--Button class="is-danger" on:click={()=>
                   confirm({action:'deleteConnection',key:'name',connection:connection})}
                    aria-label="close">Name
                  </Button-->
              <td>
               </td>
            </tr>
            {/if}
          {/each}
<!--        {#if !loading}
            <tr>
              <td colspan="4">
                <section class="section">
                  <div class="content has-text-grey has-text-centered">
                    <p><i class="far fa-3x fa-frown"></i></p>
                    <p>No data</p>
                  </div>
                </section>
              </td>
            </tr>
          {/if}
-->
        </tbody>
      </table>
    </div>
    </div>
    {/if}
  {/if}
</div>
<!--/section>

<section class="section"-->

<div class="container">
  <h1>5. Wifi-netwerk toevoegen</h1>
  <!--div class="columns is-centered"-->
  <div class="info-blok">
    <span>De onderstaande lijst met lokale wifi-netwerken bevat de wifi-netwerken
      welke door de sensorkit zijn 'gezien' ('Toon lijst').
      Als uw wifi-netwerk in de lijst voorkomt, kunt u daarbij het wachtwoord
      opgeven en met de groene plus knop (<Icon class="is-primary" icon={iconFaPlus}></Icon>) dit
      netwerk toevoegen aan de configuratie bij 4.
    </span>
  </div>
  <div class="info-blok">
    <span>U kunt ook uw wifi-netwerk handmatig toevoegen door de naam daarvan (SSID)
      hieonder in te vullen met het wachtwoord. Ook hier de plus knop voor toevoegen.
    </span>
    <div class="container">
      <div class="columns is-mobile is-centered">
        <div class="column is-narrow">
          <table class="connection-table table is-striped is-narrow">
            <thead>
              <tr>
                <th>SSID</th>
                <th>Password</th>
                <th>Connect</th>
              </tr>
            </thead>
            <tbody>
                <tr>
                  <td>
                    <input class="tdssid" name="SSID" autocapitalize="off"
                      placeholder="Wifi naam" bind:value={wifiManualConnection.ssid} />
                  </td>
                  <td>
                     <input class="password" name="Wachtwoord" xtype=password autocapitalize="off"
                      placeholder="Wifi wachtwoord" bind:value={wifiManualConnection.password} />
                  </td>
                  <td>
                    <button name="manualm1" class="plusbutton button is-primary {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                      confirm({action:'addWifiNetwork',ssid:wifiManualConnection.ssid, passwd:wifiManualConnection.password,method:'1'})}
                       aria-label="Add connection">
                       <Icon icon={iconFaPlus}></Icon>
                    </button>
                   <button name="manualm2" class="plusbutton button is-info {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                     confirm({action:'addWifiNetwork',ssid:wifiManualConnection.ssid, passwd:wifiManualConnection.password,method:'2'})}
                      aria-label="Add connection">
                      <Icon icon={iconFaPlus}></Icon>
                    </button>
                  </td>
                </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
  <div>
    <button class="button is-info {isRefreshingWifiListCache}" on:click={nmcliDeviceWifiListCache}>Toon lijst</button>
    <button class="button is-info {isRefreshingWifiList}" on:click={nmcliDeviceWifiList}>Ververs</button>
    <progress class="progress is-info" value="{wifiListProgressCount}" max="100">{wifiListProgressCount}%</progress>
  </div>
  <div class="container info-blok">
    <div class="columns is-mobile is-centered">
      <div class="column is-narrow">
      <table class="connection-table table is-striped is-narrow">
        <thead>
          <tr>
            <th>SSID</th>
            <!--th>In use</th-->
            <th>K/S</th>
            <!--th>Rate</th-->
            <!--th>S</th-->
  <!--          <th>Bars</th>
            <th>Security</th> -->
            <th>Password</th>
            <th>Connect</th>
          </tr>
        </thead>
        <tbody>
          {#each wifiListArray as accessPoint , i}
          {#if accessPoint.CHAN<14000}
          {#if accessPoint.SSID!='--'}
            <tr>
              <td class="tdssid">{accessPoint.SSID}</td>
              <!--td>{accessPoint['IN-USE']}</td-->
              <td>{accessPoint.CHAN}/{accessPoint.SIGNAL}</td>
              <!--td>{accessPoint.RATE}</td-->
              <!--td>{accessPoint.SIGNAL}</td-->
      <!--        <td>{accessPoint.BARS}</td>
              <td>{accessPoint.SECURITY}</td>
-->               <td>
                 <input class="password" name="Wachtwoord" xtype=password autocapitalize="off"
                  placeholder="Wifi wachtwoord" bind:value={accessPoint.password} />
              </td>
              <td>
                <button name="m1" class="plusbutton button is-primary {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                  confirm({action:'addWifiNetwork',ssid:accessPoint.SSID, passwd:accessPoint.password,method:'1'})}
                   aria-label="Add connection">
                   <Icon icon={iconFaPlus}></Icon>
                </button>
               <button name="m2" class="plusbutton button is-info {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                 confirm({action:'addWifiNetwork',ssid:accessPoint.SSID, passwd:accessPoint.password,method:'2'})}
                  aria-label="Add connection">
                  <Icon icon={iconFaPlus}></Icon>
                </button>
              </td>
            </tr>
            {/if}
            {/if}
          {/each}
<!--        {#if !loading}
            <tr>
              <td colspan="4">
                <section class="section">
                  <div class="content has-text-grey has-text-centered">
                    <p><i class="far fa-3x fa-frown"></i></p>
                    <p>No data</p>
                  </div>
                </section>
              </td>
            </tr>
          {/if}
-->
        </tbody>
      </table>
    </div>
  </div>
</div>
</div>

<div class="container info-blok">
    <span>
      <i class="fas fa-info"></i><b>Uw wifi-netwerk staat niet in de lijst?:</b>
      <i>Met de "Ververs"-knop kunt u de lijst met lokale wifi-netwerken opnieuw
