      <br/><br/>
      <i class="fas fa-info"></i><b>Lijst blijft leeg?:</b>
      <i>Als er geen lokale wifi-netwerken worden getoond, controleer dan bij 3.
        of er nog verbinding is met de sensorkit service en of de wifi-verbinding
        nog goed staat.</i>
    </span>
    <br/><br/>
    <br/><br/>
</div>
